import  React, { Component } from 'react';
import { Field, reduxForm } from 'redux-form';
import './bootstrap.css';
import './loginForm.css';
import './animate.css';
import { connect } from 'react-redux';

class LoginForm extends Component{


    render() {
      const {handleSubmit} = this.props;
      let mod = '';
      let butMod = '';
      let formMod = '';
      let loggedMod = 'invisible absolute';
      if (this.props.auth === "Denied"){
        mod = 'border border-danger animated bounce absolute'
      }
      if (this.props.auth === "Logged"){
        formMod = 'animated rollOut';
        loggedMod = 'animated rollIn zindex-tooltip alert alert-success absolute'
      }
      if (!this.props.isPageLoaded){
        butMod = 'animated zoomOut';
        mod = ''
      }

      const Button = (loaded)  => {
        if (loaded) return <button className={"btn btn-primary" + " " + butMod} type="submit">Submit →</button>;
        return <button className="btn btn-primary animated zoomIn">Loading...</button>
      };


      return (
        <div>
        <div className={"container w-25 text-center mt-5 p-3 bg-light rounded animated bounceInDown"+formMod} >
          <div className='d-flex justify-content-center align-items-center'>
            <div className='logo-wrapper'>
              <div className='circle c1 animated pulse infinite'> </div>
              <div className='circle c2 animated pulse infinite'> </div>
              <div className='circle c3 animated pulse infinite'> </div>
              <div className='circle c4 animated pulse infinite'> </div>
            </div>
            <h2 className='text-center d-block pl-2'>Login</h2>
          </div>
          <form onSubmit={handleSubmit} >
            <div className='form-group text-left' >
              <label htmlFor="Username">Login</label>
              <Field className={'form-control'+ ' ' +  mod} name="Username" component="input" type="text"/>
            </div>
            <div className='form-group text-left' >
              <label htmlFor="pass">Password</label>
              <Field className={'form-control'+ ' ' +  mod} name="Password" component="input" type="text"/>
            </div>
            <div>{Button(this.props.isPageLoaded)}</div>
          </form>
        </div>
          <div className={loggedMod}>
            <span>Successful logged</span>
          </div>
        </div>
      );
    }
}


function mapStateToProps(state) {
  return {
    auth: state.loginForm.auth,
    isPageLoaded: state.loginForm.isPageLoaded
  };
}


export default connect(mapStateToProps)(reduxForm({
  form: 'LoginForm'
})(LoginForm))




