const initialState = {
  auth: '',
  language: '0',
  requestError: '',
  isPageLoaded: true,
};

export default function loginForm(state = initialState, action) {
  switch (action.type) {
    case 'LOGIN_REQUEST_SUCCESS':
      return { ...state, isPageLoaded: true, auth: action.auth, language: action.language };

    case 'LOADING':
      return { ...state, isPageLoaded: false };

    case 'FAIL':
      return { ...state, requestError: action.requestError };

    default:
      return state;
  }
}



